.card {
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
    max-width: 300px;
    margin: auto;
    text-align: center;
}

.title {
    color: grey;
    font-size: 18px;
}

a.account-fa {
    text-decoration: none;
    font-size: 22px;
    color: black;
    display: inline-block !important;
}

button:hover,
a.account-fa:hover {
    opacity: 0.7;
}

button.account {
    border: none;
    outline: 0;
    display: inline-block;
    padding: 8px;
    color: white;
    background-color: #000;
    text-align: center;
    cursor: pointer;
    width: 100%;
    font-size: 18px;
}

p.contact-btn-container {
    margin-top: 10px;
}